  listParts() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listParts/' + this.proCode, options)
      .subscribe(data => {
        var partsListInfo = data.json();
        if (partsListInfo.status == 200) {
          //console.log("partsListInfo" + JSON.stringify(partsListInfo))
          this.partsList = partsListInfo.response;
          //console.log("list parts Info" + JSON.stringify(this.partsList))
          // return false;
          this.partStatus = true
        } else {
          this.partStatus = false
        }
      })
  }

