  searchFunctionality() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    let obj = {
      "partId": this.searchPartsValue.value.partId,
      "vendor": this.searchPartsValue.value.vendor,
      "partCategory": this.searchPartsValue.value.partCategory,
      "manufacturer": this.searchPartsValue.value.manufacturer,
      "model": this.searchPartsValue.value.model,
      "partSubCategory": this.searchPartsValue.value.partSubCategory,
      "partName": this.searchPartsValue.value.partName,
      "totalQty": this.searchPartsValue.value.totalQty,
    }
    //console.log(obj)
    this.http.post(Global.url + '/searchParts', [
      { id: 'partId', value: obj.partId },
      { id: 'vendor', value: obj.vendor },
      { id: 'partCategory', value: obj.partCategory },
      { id: 'manufacturer', value: obj.manufacturer },
      { id: 'model', value: obj.model },
      { id: 'partSubCategory', value: obj.partSubCategory },
      { id: 'partName', value: obj.partName },
      { id: 'totalQty', value: obj.totalQty }
    ], options).subscribe((data: any) => {
      this.partsSearch = data.json();
      console.log("Parts Search" + JSON.stringify(this.partsSearch))
      if (this.partsSearch.status == 200) {
        this.partsList = this.partsSearch.response;
        //console.log("searchPartsData values" + JSON.stringify(this.partsList))
        this.partStatus = true;
     this.searchPartsValue.reset();
      } else {
        this.partStatus = false;
        //console.log('The else')
      }
    })
  }

