import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  providers: [Beyond],
  selector: 'app-list-inventory-details',
  templateUrl: './list-inventory-details.component.html',
  styleUrls: ['./list-inventory-details.component.scss']
})
export class ListInventoryDetailsComponent implements OnInit {

  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
  }

  backToListOfInventory() {
    this.router.navigate(['/inventory/listInventory'])
  }

}
