import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  providers: [Beyond],
  selector: 'app-view-service-catalog',
  templateUrl: './view-service-catalog.component.html',
  styleUrls: ['./view-service-catalog.component.scss']
})
export class ViewServiceCatalogComponent implements OnInit {

  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
   ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
  }

  okServiceCatalog(){
    this.router.navigate(['/inventory/listServiceCatalogs'])  
  }

  editServiceCatalog(){
    this.router.navigate(['/inventory/editServiceCatalog'])  
  }

}
