import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ListInventoryComponent } from './inventory/list-inventory/list-inventory.component';
import { InventoryLayoutComponent } from './inventory-layout/inventory-layout.component';
import { ListInventoryDetailsComponent } from './inventory/list-inventory-details/list-inventory-details.component';
import { AddInventoryComponent } from './inventory/add-inventory/add-inventory.component';
import { ViewInventoryComponent } from './inventory/view-inventory/view-inventory.component';
import { EditInventoryComponent } from './inventory/edit-inventory/edit-inventory.component';
import { LocateMoveInventoryComponent } from './inventory/locate-move-inventory/locate-move-inventory.component';
import { AdjustQtyInventoryComponent } from './inventory/adjust-qty-inventory/adjust-qty-inventory.component';
import { AddPartComponent } from './inventory/parts/add-part/add-part.component';
import { AddToolComponent } from './inventory/tools/add-tool/add-tool.component';
import { AddConsumableComponent } from './inventory/consumables/add-consumable/add-consumable.component';
import { AddServiceCatalogComponent } from './inventory/service-catelog/add-service-catalog/add-service-catalog.component';
import { ListServiceCatalogsComponent } from './inventory/service-catelog/list-service-catalogs/list-service-catalogs.component';
import { ViewServiceCatalogComponent } from './inventory/service-catelog/view-service-catalog/view-service-catalog.component';
import { EditServiceCatalogComponent } from './inventory/service-catelog/edit-service-catalog/edit-service-catalog.component';

import { EditToolComponent } from './inventory/tools/edit-tool/edit-tool.component';
import { ViewToolComponent } from './inventory/tools/view-tool/view-tool.component';
import { EditPartComponent } from './inventory/parts/edit-part/edit-part.component';
import { ViewPartComponent } from './inventory/parts/view-part/view-part.component';
import { EditConsumableComponent } from './inventory/consumables/edit-consumable/edit-consumable.component';
import { ViewConsumableComponent } from './inventory/consumables/view-consumable/view-consumable.component';
import { PartsToolsConsumablesComponent } from './inventory/parts-tools-consumables/parts-tools-consumables.component';
import { ApproveInventoryComponent } from './inventory/approve-inventory/approve-inventory.component';
import { TruckOrderListComponent } from './inventory/truck-order-list/truck-order-list.component';
import{AddPoComponent} from './inventory/add-po/add-po.component'
import{ListPoComponent} from './inventory/list-po/list-po.component'
import { EditPoComponent } from './inventory/edit-po/edit-po.component';
import { ViewPoComponent } from './view-po/view-po.component';

const routes: Routes = [
  {
    path: '', component: InventoryLayoutComponent,

    children: [
      {
        path: 'listInventory',
        component: ListInventoryComponent
      },
      {
        path: 'listInventoryDetails',
        component: ListInventoryDetailsComponent
      },
      {
        path: 'addInventory',
        component: AddInventoryComponent
      },
      {
        path: 'viewInventory/:itemId',
        component: ViewInventoryComponent
      },
      {
        path: 'editInventory/:itemId',
        component: EditInventoryComponent
      },
      {
        path: 'locateMoveInventory',
        component: LocateMoveInventoryComponent
      },
      {
        path: 'adjustQtyInventory',
        component: AdjustQtyInventoryComponent
      },
      {
        path: 'addPart',
        component: AddPartComponent
      },
      {
        path: 'addTool',
        component: AddToolComponent
      },
      {
        path: 'addConsumable',
        component: AddConsumableComponent
      },
      {
        path: 'listOfPo',
        component: ListPoComponent,
      },
      {
        path: 'addPo/:billingAddressData',
        component:  AddPoComponent ,
      },
      {
        path: 'addServiceCatalog',
        component: AddServiceCatalogComponent
      },
      {
        path: 'listServiceCatalogs',
        component: ListServiceCatalogsComponent
      },
      {
        path: 'viewServiceCatalog',
        component: ViewServiceCatalogComponent
      },
      {
        path: 'editServiceCatalog',
        component: EditServiceCatalogComponent
      },
      {
        path: 'partsToolsConsumables',
        component: PartsToolsConsumablesComponent
      },
      {
        path: 'editTool/:toolId',
        component: EditToolComponent
      },
      {
        path: 'viewTool/:toolId',
        component: ViewToolComponent
      },
   
      {
        path: 'editConsumable/:consumableId',
        component: EditConsumableComponent
      },
      {
        path: 'viewConsumable/:consumableId',
        component: ViewConsumableComponent
      },
   
      {
        path: 'editPart/:partId',
        component: EditPartComponent
      },
      {
        path: 'viewPart/:partId',
        component: ViewPartComponent
      },
      {
        path: 'ApproveInventory',
        component: ApproveInventoryComponent
      },
      {
        path: 'TruckOrderList',
        component: TruckOrderListComponent
      },
      {
        path: 'editPo/:poId',
      component: EditPoComponent
      },
    
      {
        path: 'viewPo/:poId',
        component: ViewPoComponent
      },
    

    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class InventoryRoutingModule { }
