  /* cheeeck tool Name */
  mailIdVerification() {
    console.log("This is the Group Name:" + JSON.stringify(this.editConsumable.value.consumableName))
    if (this.editConsumable.value.consumableName == "") {
      this.toastr.warning("consumableName Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      // var url = Global.url + '/checkEquipmentItemType/' + this.addEquipmentItem.value.equipmentItemType + "/" + this.loginData;
      // console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checkconsumableName/' + this.editConsumable.value.consumableName + "/" + this.proCode)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
