  markUp() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listPBMarkup/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var equipAgreement = data.json();
        if (equipAgreement.status == 200) {
          //console.log("agreementStatus Details" + JSON.stringify(equipAgreement))
          this.listmarkup = equipAgreement.response;
          for (var i = 0; i < this.listmarkup.length; i++) {
            if (this.listmarkup[i].category == "Material") {
              this.materialMarkUp = this.listmarkup[i].markUp
              //console.log("materialMarkUp"+this.materialMarkUp) 
            }
          }

          //console.log("list id" + JSON.stringify(this.listmarkup))
          //return false;
          this.mark = true
        } else {
          this.mark = false
        }
      }, error => {
        //  this.toastr.warning('Unable to connect to server');
      })
  }

  getListPrice() {

    this.markupTotalValue = 0;
    this.priceValue = 0
    console.log("this is smmmm" + this.markupTotalValue)
  }
  getPriceValue(price) {
    console.log(price)
    if (price == 0) {
      console.log("if block")
      this.markupTotalValue = 0;

      this.totalValue = 0 * this.materialMarkUp / 100;
      console.log("This is totalValue price" + price * this.materialMarkUp / 100)
      console.log("This is totalValue price" + this.totalValue)
      console.log("This is list price" + this.markupTotalValue)
    }
    else {
      console.log("else block")
      this.markupTotalValue = 0;

      this.totalValue = price * this.materialMarkUp / 100;
      console.log("This is totalValue price" + price * this.materialMarkUp / 100)
      console.log("This is totalValue price" + this.totalValue)
      this.markupTotalValue = price + this.totalValue;

      console.log("This is list price" + this.markupTotalValue)
    }
  }


  // generate  automatic partId
  getConsumableData() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + "/generateConsumableID/"+this.loginData.loginCode, options)
      .subscribe(data => {
        this.consumeData = data.json();
        if (this.consumeData.status == 200) {
          this.consumableID = this.consumeData.consumableId;
          //console.log("Records Found"+ this.consumableID );
          this.dataConsumeInfo = true;
        } else {
          this.dataConsumeInfo = false;
          //console.log("NO Records Found");
        }
      });
  }
