  submitImage() {
    console.log("consumableMainCategory" + this.addConsumable.value.consumableMainCategory)
    console.log("consumableSubCategory" + this.addConsumable.value.consumableSubCategory)
    console.log("consumableName" + this.addConsumable.value.consumableName)
    console.log("manufacturer" + this.addConsumable.value.manufacturer)
    console.log("model" + this.addConsumable.value.model)
    console.log("lastPurchasedDate" + this.addConsumable.value.lastPurchasedDate)
    if (this.addConsumable.value.consumableMainCategory != undefined && this.addConsumable.value.consumableSubCategory != undefined && this.addConsumable.value.consumableName !== null && this.addConsumable.value.manufacturer !== null && this.addConsumable.value.model !== null && this.addConsumable.value.lastPurchasedDate !== null) {


      let obj = {
        "consumableId": this.consumableID,
        "proId": this.proCode,
        "sku": this.addConsumable.value.sku,
        "vendor": this.addConsumable.value.vendor,
        "price": this.addConsumable.value.price,
        "categoryCode": this.addConsumable.value.consumableMainCategory,
        "consumableMainCategory": '' + this.proCatNameData[0].categoryName,
        "consumableSubCategory": '' + this.proCatNameData[0].subcategoryName,
        "subCatogryCode": this.addConsumable.value.consumableSubCategory,
        "consumableName": this.addConsumable.value.consumableName,
        "description": this.addConsumable.value.description,
        "manufacturer": this.addConsumable.value.manufacturer,
        "model": this.addConsumable.value.model,
        "size": this.addConsumable.value.size,
        "color": this.addConsumable.value.color,
        "lastPurchasedDate": this.addConsumable.value.lastPurchasedDate,
        "consumablePicture": Global.documentName,
        "createdBy": "" + this.loginData.emailId,
        "materialMarkupPercentage": '' + this.materialMarkUp,
        "listPrice": '' + this.markupTotalValue  
      }
      console.log("CONS obj" + JSON.stringify(obj))
      this.http.post(Global.url + '/addConsumables/' + this.loginData.loginCode + "/" + this.consumableID + "/" + this.addConsumable.value.consumableName, obj)
        .subscribe(data => {
          var ConsData = data.json();
          if (ConsData.status == 200) {
            //console.log("vanData" + JSON.stringify(ConsData))
            this.toastr.success('consumable Added Successfuly...');
            this.router.navigate(['/inventory/partsToolsConsumables'])
          } else {
            this.toastr.warning('Failed t Add Record');
          }
        }, error => {
          //console.log(error);// Error getting the data
        });
    }

  }

  cancelButton() {
    this.router.navigate(['/inventory/partsToolsConsumables'])
  }

  handleImage(file) {
    var picReader = new FileReader();
    picReader.onload = (e: any) => {
      var reader = e.target;
      this.imagePreview = reader.result;
    }
    picReader.readAsDataURL(file)
  }

  select(event) {
    let reader = new FileReader();
    if (event.target.files && event.target.files.length > 0) {
      var file = event.target.files[0];
      this.img = true;
      this.progressShow = false;
      this.progressHide = false;
      this.file2Upload = event.target.files[0];
      this.handleImage(file)
    } else {
      this.img = false;
      this.progressShow = false;
      this.progressHide = false;
      this.imagePreview = "assets/img/no-image.png"
    }
  }

