  /* pro specific Categories */
  getProCategories() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/proPricebookCategories/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var proCategories = data.json();
        if (proCategories.status == 200) {
          this.categoryDet = proCategories.response;

          // this.proCategoryStatus = true
        } else {
          // this.labourStatus = false
        }
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
