import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { ImageUpload } from '../../../../utilities/imageupload'
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../../global/global';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
declare var $: any;
@Component({
  providers: [Beyond],
  selector: 'app-edit-po',
  templateUrl: './edit-po.component.html',
  styleUrls: ['./edit-po.component.scss']
})
export class EditPoComponent implements OnInit {
  displayFileModalOrderList: string;
  loginData: any;
  displayFileView: string;
  showTruck: any = [];
  inventoryListResponse: boolean;
  addPoData: FormGroup;
  selectedInventory: any = [];
  inventoryData: any = [];
  newInventoryData: any = [];
  addnewPo: FormGroup;
  qtyData: any;
  WO: any = [];
  poId: any;
  addToPoData: boolean;
  addnewPoData: boolean;
  vendorData: any = [];
  vendorDetails: any;
  groupList: any;
  taxValue: number;
  listPriceInventory: any;
  taxPercentage: any;
  taxvalueData: any;
  totalmaterialPrice: any;
  data: any = []
  tax: any;
  priceData: any = [];
  priceValue: any;
  quantityValue: any;
  totalValue: number = 0;
  workOrderNumber: string;
  viewPoDatas: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private route: ActivatedRoute,
    public http: Http,
    private formBuilder: FormBuilder,
    public toastr: ToastrService,
    private activatedRoute: ActivatedRoute,
  ) {
  }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.poId = this.activatedRoute.snapshot.params['poId']
    this.getInventoryList()
    this.getWoData()
    this.getVendorData()
    this.getPOID()
    this.listGroupDetails()
    this.getView()
    this.getViewInventoryData()
    this.addnewPo = this.formBuilder.group({
      mySwitch: [true],
      workOrderNumber: [""],
      poId: [""],
      dateCreated: [""],
      staffName: [""],
      customerName: [""],
      address: [""],
      state: [""],
      city: [""],
      zipCode: [""],
      custPhone: [""],
      jobTitle: [""],
      vendor: [""],
      streetAddress: [""],
      vendorCity: [""],
      vendorState: [""],
      vendorzipCode: [""],
      vendorPhone: [""],
    })
    this.addPoData = this.formBuilder.group({
      fromPOData: this.formBuilder.array([this.createPriceBookInfo()])
    })
  }

  /* view Data */

  getView() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getPOView/' + this.poId, options)
      .subscribe(data => {
        var viewPoData = data.json();
        this.viewPoDatas = viewPoData.response[0];
        console.log("SSSSS" + JSON.stringify(this.viewPoDatas))
        this.editData(this.viewPoDatas)
        if (this.viewPoDatas.status == 200) {

        }
        else {
          this.inventoryListResponse = false;
        }
        error => {
        }
      })
  }
  getViewInventoryData() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getPOInventoryView/' + this.poId, options)
      .subscribe(data => {
        var viewPoData = data.json();
        this.selectedInventory = viewPoData.response[0];
        console.log("selectedInventory" + JSON.stringify(this.selectedInventory))
        if (this.selectedInventory.status == 200) {

        }
        else {
          this.inventoryListResponse = false;
        }
        error => {
        }
      })
  }

  editData(x) {
    this.addnewPo = this.formBuilder.group({
      customerName: [x.customerName, Validators.required],
      workOrderNumber: [x.workOrderNumber, Validators.required],
      dateCreated: [x.dateCreated, Validators.required],
      staffName: [x.staffName, Validators.required],
      custPhone: [x.custPhone, Validators.required],
      address: [x.address, Validators.required],
      state: [x.state, Validators.required],
      city: [x.city, Validators.required],
      zipCode: [x.zipCode, Validators.required],
      jobTitle: [x.jobTitle, Validators.required],
      vendor: [x.vendor, Validators.required],
      streetAddress: [x.streetAddress, Validators.required],
      vendorCity: [x.vendorCity, Validators.required],
      vendorState: [x.vendorState, Validators.required],
      vendorzipCode: [x.vendorzipCode, Validators.required],
      vendorPhone: [x.vendorPhone, Validators.required],
    });

  }
  /* view Data */

  addOrders() {
    this.displayFileModalOrderList = 'block'
  }

  CancelDetails() {
    this.displayFileView = 'none'
  }
  cancelButton() {
    this.router.navigate(['/inventory/listOfPo'])
  }
  home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  getInventoryList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfInventory' + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          console.log("showTruck" + JSON.stringify(this.showTruck))
          this.showTruck = response.response;
          this.inventoryListResponse = true;
        }
        else {
          this.inventoryListResponse = false;
        }
        error => {
        }
      })
  }

  /*pop up data selection and storing start here */
  selectInventoryData() {
    this.displayFileView = 'block'
  }
  createPriceBookInfo(): FormGroup {
    return this.formBuilder.group({
      item: ['', Validators.required],
      type: ['', Validators.required],
      description: ['', Validators.required],
      manufacturer: ['', Validators.required],
      model: ['', Validators.required],
      name: ['', Validators.required],
      quantity: ['', Validators.required],
      listPrice: ['', Validators.required],
      subtotal: ['', Validators.required],

    });
  }

  selectServiceFromList(data) {
    if (data.selectedService === true) {
      this.selectedInventory.push(data)
      for (var i = 0; i < this.selectedInventory.length; i++) {
        this.selectedInventory[i].sno = i + 1;
      }

    } else {
      this.selectedInventory = this.selectedInventory.filter(value => value.selectedService == true);
      this.selectedInventory.forEach((e) => {
      })
    }
  }
  saveSelectedInventory() {
    this.inventoryData.push(this.selectedInventory)
    for (var i = 0; i < this.inventoryData.length; i++) {
      this.newInventoryData = this.inventoryData
    }

    this.displayFileView = 'none'
  }
  getQuantity(data) {
    this.priceData = 0
    this.totalValue = 0
    this.priceValue = data.price
    this.quantityValue = data.quantity
    data.priceData = this.quantityValue * this.priceValue
    this.selectedInventory.forEach((e) => {
      this.totalValue = this.totalValue + parseInt(e.priceData)
    })
    this.taxPercentage = this.groupList[0].salesTax
    this.taxValue = this.totalValue * this.taxPercentage / 100
    this.taxvalueData = this.totalValue + this.taxValue
  }

  /*WO Data Here */
  getWoData() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getWOPODATA/' + this.workOrderNumber, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.WO = response.response[0];
          this.addnewPo = this.formBuilder.group({
            customerName: [this.WO.customerName, Validators.required],
            workOrderNumber: [this.WO.workOrderNumber, Validators.required],
            dateCreated: [this.WO.createdOn, Validators.required],
            staffName: [this.WO.addPrimaryTech, Validators.required],
            custPhone: [this.WO.phoneNumber, Validators.required],
            address: [this.WO.address, Validators.required],
            state: [this.WO.state, Validators.required],
            city: [this.WO.city, Validators.required],
            zipCode: [this.WO.zipCode, Validators.required],
            jobTitle: [this.WO.jobTitle, Validators.required],
            vendor: [this.WO.jobTitle, Validators.required],
          });
          this.inventoryListResponse = true;
        }
        else {
          this.inventoryListResponse = false;
        }
        error => {
        }
      })
  }
  getVendorData() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfVendors/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.vendorData = response.response;
          this.inventoryListResponse = true;
        }
        else {
          this.inventoryListResponse = false;
        }
        error => {
        }
      })
  }
  vendorNameData(x) {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getVendorName/' + this.loginData.loginCode + '/' + x, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.vendorDetails = response.response[0];
          this.addnewPo = this.formBuilder.group({
            streetAddress: [this.vendorDetails.streetAddress, Validators.required],
            vendorCity: [this.vendorDetails.city, Validators.required],
            vendorState: [this.vendorDetails.state, Validators.required],
            vendorzipCode: [this.vendorDetails.zipCode, Validators.required],
            vendorPhone: [this.vendorDetails.phoneNumber, Validators.required],
          });
          this.inventoryListResponse = true;
        }
        else {
          this.inventoryListResponse = false;
        }
        error => {
        }
      })
  }
  /*WO Data Here */

  getPOID() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/autoPoId/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        this.poId = response.poData;
      }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }

  /* -- Add Po data */
  onSubmit() {
    this.addnewPoData = true;
    this.addPurchaseData()
    // stop here if form is invalid
    if (this.addnewPo.invalid) {
      this.addPurchaseData()
      return;
    }
  }
  get h() { return this.addnewPo.controls; }

  addPurchaseData() {
    let addObj = {
      "proId": this.loginData.loginCode,
      "poId": this.poId,
      "customerName": this.addnewPo.value.customerName,
      "staffName": this.addnewPo.value.staffName,
      "workOrderNumber": this.addnewPo.value.workOrderNumber,
      "dateCreated": this.addnewPo.value.dateCreated,
      "custPhone": this.addnewPo.value.custPhone,
      "address": this.addnewPo.value.address,
      "state": this.addnewPo.value.state,
      "city": this.addnewPo.value.city,
      "zipCode": this.addnewPo.value.zipCode,
      "jobTitle": this.addnewPo.value.jobTitle,
      "vendor": this.addnewPo.value.vendor,
      "streetAddress": this.addnewPo.value.streetAddress,
      "vendorCity": this.addnewPo.value.vendorCity,
      "vendorState": this.addnewPo.value.vendorState,
      "vendorzipCode": this.addnewPo.value.vendorzipCode,
      "vendorPhone": this.addnewPo.value.vendorPhone,
      "fromPOData": this.selectedInventory == [] ? "" : this.selectedInventory,
      "createdOn": new Date(),
      "createdBy": '' + this.loginData.emailId,

    }
    console.log("OBJ" + JSON.stringify(addObj))
    this.http.post(Global.url + '/editnewPoData', addObj)
      .subscribe(data => {
        var inventoryLog = data.json();
        if (inventoryLog.status == 200) {
          this.router.navigate(['/inventory/listOfPo'])
          this.toastr.success('Updated PO Successfuly...');
        } else {
        }
      }, error => {
        console.log(error);// Error getting the data
      });

    //  }
  }
  /* -- /Add Po data */

  listGroupDetails() {
    var headers = new Headers();
    headers.append("Accept", "application/json");
    headers.append("Content-Type", "application/json");
    let options = new RequestOptions({ headers: headers });
    this.http
      .get(Global.url + "/getStaffGroups/" + this.loginData.loginCode, options)
      .subscribe((data) => {
        var response = data.json();
        if (response.status == 200) {
          this.groupList = response.response;
          this.tax = this.groupList[0].salesTax
        } else {
        }
      });
  }

}
