import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { ImageUpload } from '../../../../utilities/imageupload'
import { Global } from '../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';


@Component({
  providers: [Beyond],
  selector: 'app-approve-inventory',
  templateUrl: './approve-inventory.component.html',
  styleUrls: ['./approve-inventory.component.scss']
})
export class ApproveInventoryComponent implements OnInit {
  inventoryListDetails: any = [];
  inventoryListResponse: boolean;
  loginData: any;
  displayFileModalApprove: string;

  constructor(
    public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,

  ) {

  }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    //console.log("loginData" + JSON.stringify(this.loginData.loginCode))
    this.getInventoryList();
  }
  getInventoryList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/adjustInventoryItem' + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        console.log("listOfInventory" + JSON.stringify(response))
        if (response.status == 200) {
          this.inventoryListDetails = response.response;
          console.log("listOfInventory:::" + JSON.stringify(this.inventoryListDetails))
          this.inventoryListResponse = true;
        }
        else {
          this.inventoryListResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  moveLocationWiseItem() {
    this.displayFileModalApprove = 'block'
  }

  moveItem() {
    this.displayFileModalApprove = 'none'
  }
  AddLocationWiseItem() {
    this.displayFileModalApprove = 'none'
  }
  CancelItemDetails() {
    this.displayFileModalApprove = 'none'
  }
}
