  viewPart() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewParts/' + this.partID, options)
      .subscribe(data => {
        var viewPartData = data.json();
        this.partData = viewPartData.response[0];
        console.log("View data" + JSON.stringify(this.partData))
        if (this.partData.partImage == "assets/img/no-image.png") {

          this.imagePreview = "assets/img/no-image.png"
          Global.imagePath = this.partData.partImage
          //console.log("Part Image" + this.imagePreview)
        } else {
          this.partImg = this.partData.partImage
          this.imagePreview = Global.url + "/" + this.partData.partImage
          console.log("Part Image1" + this.partImg)
          Global.imagePath = this.partData.partImage
        }
        this.editData(this.partData)

        //console.log("View Part Data" + JSON.stringify(this.partData))
        this.dataVan = true;

      }, error => {
        this.toastr.warning('Unable to Connect to Server ')
      })
  }
