  mailIdVerification() {
    console.log("This is the part Name:" + JSON.stringify(this.editPart.value.partName))
    if (this.editPart.value.partName == "") {
      this.toastr.warning("partName Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      // var url = Global.url + '/checkEquipmentItemType/' + this.addEquipmentItem.value.equipmentItemType + "/" + this.loginData;
      // console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checkpartName/' + this.editPart.value.partName + "/" + this.proCode)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }

