  quantityToadd(qtyData,) {
    if (qtyData == 0 || this.existQty == 0) {
      this.qtyData == 0;
      this.totalQuantityData = 0;
    }
    else if (qtyData == '' || this.existQty == '') {
      this.qtyData == 0;
      this.totalQuantityData = 0;
    }
    else {
      this.totalQuantityData = parseInt(this.existQty) + parseInt(qtyData);
      console.log("This is totalValue price" + this.totalQuantityData)
    }
  }
