import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../../utilities/beyond';
import { Global } from '../../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { of, interval } from 'rxjs';
import { map, delay, startWith } from 'rxjs/operators';
@Component({
  providers: [Beyond],
  selector: 'app-view-part',
  templateUrl: './view-part.component.html',
  styleUrls: ['./view-part.component.scss']
})
export class ViewPartComponent implements OnInit {
  partID: any;
  loginData: any;
  proCode: any;
  tableViewPartData: any;
  imagePreview: string = "assets/img/no-image.png";
  // imageUrl$ = interval(5000).pipe(map(i => {
  //   if (i % 2 === 0) {
  //     return ' http://localhost:8888/partRepair.png';
  //   }
  //   return 'https://lorempixel.com/800/600/animals/'
  // }),
  //   startWith(' http://localhost:8888/partRepair.png'));
  constructor(
    public beyond: Beyond,
    public router: Router,
    private route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService
  ) { }

  ngOnInit() {
    const imageUrl$ = interval(5000).pipe(map(i => {
    if (i % 2 === 0) {
      return ' http://localhost:8888/partRepair.png';
    }
    return 'https://lorempixel.com/800/600/animals/'
  }),
    startWith(' http://localhost:8888/partRepair.png'));
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proCode = this.loginData.loginCode
    this.partID = this.route.snapshot.params['partId'];
    this.viewPartData()
  }
  viewPartData() {
    //console.log("view called" + this.partID)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewParts/' + this.partID, options)
      .subscribe(data => {
        var viewPartInfo = data.json();
        this.tableViewPartData = viewPartInfo.response[0];
        //console.log("vanData" + JSON.stringify(this.tableViewPartData))
        if (this.tableViewPartData.partImage == "assets/img/no-image.png") {

          this.imagePreview = this.tableViewPartData.partImage
         
          console.log("resume" + this.imagePreview)
        } else {
          this.imagePreview = Global.url +"/" +this.tableViewPartData.partImage
          // resume1http://localhost:8888/uploads/33f46c179cb54c20bf5a0a5a05ea8188
          console.log("resume1" + this.imagePreview)
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  okButton() {
    this.router.navigate(['/inventory/partsToolsConsumables',])
  }
  editButton() {
    this.router.navigate(['/inventory/editPart', this.partID])
  }

  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  list(){
    this.router.navigate(['/inventory/partsToolsConsumables'])
  }
}
