import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../../utilities/beyond';
import { Global } from '../../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
@Component({
  providers: [Beyond],
  selector: 'app-view-consumable',
  templateUrl: './view-consumable.component.html',
  styleUrls: ['./view-consumable.component.scss']
})
export class ViewConsumableComponent implements OnInit {
  consumableId: any;
  loginData: any;
  proCode: any;
  viewConsInfo: boolean = false;
  tableViewconsumableData: any;
  imagePreview: string = "assets/img/no-image.png";
  constructor(public beyond: Beyond,
    public router: Router,
    private route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService) { }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proCode = this.loginData.loginCode;
    this.consumableId = this.route.snapshot.params['consumableId'];
    this.viewconsumableData()
  }
  viewconsumableData() {
    //console.log("view called" + this.consumableId)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewConsData/' + this.consumableId, options)
      .subscribe(data => {
        var viewconsumableInfo = data.json();
        if (viewconsumableInfo.status == 200) {
          this.viewConsInfo = true;
          this.tableViewconsumableData = viewconsumableInfo.response[0];
          //console.log("vanData" + JSON.stringify(this.tableViewconsumableData))
          if (this.tableViewconsumableData.consumablePicture == "assets/img/no-image.png") {
            this.imagePreview = this.tableViewconsumableData.consumablePicture
            //console.log("resume" + this.imagePreview)
          } else {
            this.imagePreview = Global.url + "/"+ this.tableViewconsumableData.consumablePicture
            //console.log("resume1" + this.imagePreview)

          }
        } else {
          this.viewConsInfo = false;
          this.toastr.warning('No Records Found')
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  okButton() {
    this.router.navigate(['/inventory/partsToolsConsumables'])
  }

  editButton() {
    this.router.navigate(['/inventory/editConsumable/', this.consumableId])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  list(){
    this.router.navigate(['/inventory/partsToolsConsumables'])
  }
}

