  //IMAGE UPLOAD
  imgfileUpload() {
    console.log("imgfileUpload calling")
    const url = Global.url + "/imageUploadData";
    const formData = new FormData();
    var xhr = new XMLHttpRequest();
    formData.append("image", this.file2Upload);
    const config = {
      headers: {
        "content-type": "multipart/form-data"
      }
    };
    xhr.open("post", url, true);
    xhr.upload.onprogress = e => {
      if (e.lengthComputable) {
        this.percentage = (e.loaded / e.total) * 100;
        this.progressShow = true;
        this.progressHide = false;
        if (this.percentage === 100) {
          this.progressShow = false;
          this.progressHide = true;
          // this.imageUploadVariable.nativeElement.value = "";
          this.img1 = false;
        }
      }
    };
    xhr.onerror = function (e) { };
    xhr.onload = function () {
      var a = JSON.parse(xhr.responseText);
      Global.secimagePath = a.imagePath;
      Global.documentName = a.name;

      console.log("GLObaL NAme" + Global.documentName)
    };
    xhr.send(formData);
  }
