import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Beyond } from '../../../../../utilities/beyond';
import { Global } from '../../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
@Component({
  providers: [Beyond],
  selector: 'app-view-tool',
  templateUrl: './view-tool.component.html',
  styleUrls: ['./view-tool.component.scss']
})
export class ViewToolComponent implements OnInit {
  vanId: any;
  ToolData: any;
  imagePreview: string = "assets/img/no-image.png";
  toolId: any;
  toolID: any;
  loginData: any;
  proCode: any;
  viewInfo: boolean = false;
  constructor(public beyond: Beyond,
    public router: Router,
    private route: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService) { }

  ngOnInit() {
    this.beyond.beyondAdmin();

    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.proCode = this.loginData.loginCode
    this.toolID = this.route.snapshot.params['toolId'];
    this.viewTool()
  }
  viewTool() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewTools/' + this.toolID, options)
      .subscribe(data => {
        var viewData = data.json();
        if (viewData.status == 200) {
          this.viewInfo = true;
          this.ToolData = viewData.response[0];
          //console.log("ToolData" + JSON.stringify(this.ToolData))
          if (this.ToolData.picture == "assets/img/no-image.png") {
            this.imagePreview = this.ToolData.picture
            //console.log("resume" + this.imagePreview)
          } else {
            this.imagePreview = Global.url +"/" + this.ToolData.picture
            //console.log("resume1" + this.imagePreview)

          }
        } else {
          this.viewInfo = false;
          this.toastr.warning('No Records Found')
        }
      }, error => {
        this.toastr.warning('Unable to Connect to Server')
      })
  }
  editButton(id) {
    this.router.navigate(['/inventory/editTool', this.toolID])
  }
  okTool() {
    this.router.navigate(['/inventory/partsToolsConsumables'])
  }
  home(){
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  list(){
    this.router.navigate(['/inventory/partsToolsConsumables'])
  }
}
