  updateInventory() {
    if (this.filesToUpload.length > 0 && this.uploadFlag == true) {
      this.displyFlag = true;
      return false;
    }
    let editObj = {
      "proId": this.proCode,
      "itemId": "" + this.itemData,
      "name": '' + this.editInventory.value.name,
      "purchaseDate": '' + this.editInventory.value.purchaseDate,
      "existingQty": '' + this.editInventory.value.existingQty,
      "location": '' + this.editInventory.value.location == undefined ? '' : this.editInventory.value.location,
      "qty": '' + this.editInventory.value.qty,
      "description": '' + this.editInventory.value.description,
      "totalQuantity": '' + this.totalQuantityData == undefined ? '' : this.totalQuantityData,
      "manufacturer": '' + this.editInventory.value.manufacturer,
      "serialNo": '' + this.editInventory.value.serialNo,
      "webLinkDetails": '' + this.editInventory.value.webLinkDetails,
      "listPrice": '' + this.markupTotalValue,
      "skuNumber": '' + this.editInventory.value.sku,
      "vendor": '' + this.editInventory.value.vendor,
      "type": '' + this.editInventory.value.type,
      "unit": '' + this.editInventory.value.unit,
      "price": '' + this.editInventory.value.price,
      "itemCategoryCode": '' + this.editInventory.value.itemCategoryCode == undefined ? '' : this.editInventory.value.itemCategoryCode,
      "itemSubCategoryCode": '' + this.editInventory.value.itemSubCategoryCode == undefined ? '' : this.editInventory.value.itemSubCategoryCode,
      "model": '' + this.editInventory.value.model,
      "size": '' + this.editInventory.value.size,
      "color": '' + this.editInventory.value.color,
      "createdBy": '' + this.emailId,
      "inventoryImage": this.uniqueArray == undefined ? '' : this.uniqueArray,
      "qrCode": '' + Global.documentName,
      "markupPercentage": '' + this.materialMarkUp,
      "createdOn": new Date(),
      "modifiedBy": '' + this.emailId,
      "modifiedOn": new Date(),
    }
    console.log("edit data obj222" + JSON.stringify(editObj))
    this.http.post(Global.url + '/editInventoryDetails', editObj)
      .subscribe(data => {
        var pricebookData = data.json();
        if (pricebookData.status == 200) {
          this.toastr.success('Inventory data Updated Successfuly...');
          this.router.navigate(['inventory/listInventory'])
        } else {
          this.toastr.warning("Failed to Update")
        }
      }, error => {
        //console.log(error);// Error getting the data
      });

  }
