  /*year Validation */


  onSelectFile(fileInput) {
    this.imageArray = []
    this.uniqueArray = []
    this.progressShow = false;
    this.progressHide = false;
    this.filesToUpload = []
    this.withImage = true;
    this.withOutImage = true;
    if (fileInput.target.files.length == 1) {
      this.filesToUpload.push(fileInput.target.files[0])
      this.img = true;
    } else {
      for (var i = 0; i < fileInput.target.files.length; i++) {
        this.img = true;
        this.filesToUpload.push(fileInput.target.files[i])
      }
    }
    var filesAmount = fileInput.target.files.length;
    for (let i = 0; i < filesAmount; i++) {
      var reader = new FileReader();
      this.img = true;
      reader.onload = (fileInput: any) => {
        this.urls.push(fileInput.target.result);
        this.imageuploadstatus = true;
      }

      reader.readAsDataURL(fileInput.target.files[i]);
    }
    this.uploadData()
  }


  uploadData() {
    const files: Array<File> = this.filesToUpload;
    this.formData = new FormData();
    for (let i = 0; i < files.length; i++) {
      this.formData.append("fileImage", files[i]);
    }
    this.http.post(Global.url + '/multiData', this.formData)
      .map(files => files.json())
      .subscribe(files => {
        for (var i = 0; i < files.imagePath.length; i++) {
          this.newArray.push(files.imagePath[i])
        }

        this.uploadFlag = true;
        this.displyFlag = false;
        this.uniqueArray = Array.from(new Set(this.newArray));
        this.chosseTypecount = this.uniqueArray.length
        this.imageNames = Object.keys(this.uniqueArray).map(key => ({ type: key, image: this.uniqueArray[key] }));
        this.arr = Object.keys(this.uniqueArray).map(key => ({ type: key, name: Global.url + "/" + this.uniqueArray[key] }));
      }
      )


  }

  removeSelectedFile(i) {
    this.arr.splice(i, 1);
    this.imageNames.splice(i, 1);
    this.uniqueArray = []
    for (var p = 0; p < this.arr.length; p++) {
      this.value = this.arr[p].name
      this.uniqueArray.push(this.arr[p].name.substring(25, 50))
      this.newArray = this.uniqueArray
      this.imageArray = this.uniqueArray
      if (this.uniqueArray.length == 0) {
        this.chosseTypecount 
      }
      else {
        this.chosseTypecount = this.uniqueArray.length
      }
    }
  }

  progressData() {
    const url = Global.url + "/multiData";
    const formData = new FormData();
    var xhr = new XMLHttpRequest();
    formData.append("image", this.file2Upload);
    const config = {
      headers: {
        "content-type": "multipart/form-data"
      }
    };
    xhr.open("post", url, true);
    this.uploadFlag = false
    this.displyFlag = false
    xhr.upload.onprogress = e => {
      if (e.lengthComputable) {
        this.percentage = (e.loaded / e.total) * 100;
        this.progressShow = true;
        this.progressHide = false;
        if (this.percentage === 100) {
          this.progressShow = false;
          this.progressHide = true;

        }
      }
    };
    xhr.onerror = function (e) { };
    xhr.onload = function () {
      var a = JSON.parse(xhr.responseText);
      Global.secimagePath = a.imagePath;
    };
    xhr.send(formData);
  }



  viewInventory() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewInventoryData' + '/' + this.itemNumber, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.viewData = response.response[0];
          this.priceBookServiceResponse = true;
          this.editData(this.viewData)
          this.getInvantorySubCategory(this.viewData.itemCategoryCode)
          this.urls = this.viewData.inventoryImage.split(',')
          console.log("url" + JSON.stringify(this.urls))
          console.log("this.viewData.inventoryImage" + JSON.stringify(this.viewData.inventoryImage))
          if (this.viewData.inventoryImage == "undefined" ) {
            console.log("1st IF ")
            this.imagePreview = '../assets/img/no-image.png'
            this.withOutImage = false;
            this.withImage = false;
            this.chosseTypecount 
            console.log("CCC"+this.chosseTypecount)
          }
          if (this.viewData.inventoryImage == "" ) {
            console.log("2nd IF ")
            this.imagePreview = '../assets/img/no-image.png'
            this.withOutImage = false;
            this.withImage = false;
            this.chosseTypecount 
            console.log("CCC"+this.chosseTypecount)
          }
          if (this.viewData.inventoryImage != "undefined" ) {
            console.log("3nd  if")
            this.withOutImage = true;
            this.withImage = true;
            console.log("withOutImage")
            for (var i = 0; i < this.urls.length; i++) {
              this.chosseTypecount = this.urls.length
              this.imageArray.push(this.urls[i])
              this.imageNames.push({ image: this.urls[i] });
              this.arrayValues.push(this.urls[i])
              this.imagePreview = Global.url + "/" + this.urls[i]
              this.arr.push({ name: this.imagePreview });
              this.newArray = this.arrayValues
              this.uniqueArray = this.arrayValues
            }
          }

          if (this.viewData.qrCode == '' || this.viewData.qrCode == null || undefined) {
            this.qrCodePreview = "assets/img/QrCode.png"

            console.log("qrCodePreview" + this.qrCodePreview)
          } else {
            this.qrCodePreview = Global.url + "/" + this.viewData.qrCode
            // resume1http://localhost:8888/uploads/33f46c179cb54c20bf5a0a5a05ea8188
            console.log("resume1" + this.qrCodePreview)
          }
        }
        else {
          this.priceBookServiceResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }

  editData(x) {
    this.totalQuantityData = x.totalQuantity
    this.itemData = x.itemId
    this.markupTotalValue = x.listPrice
    console.log("TTTTT" + this.markupTotalValue)
    this.editInventory = this.formBuilder.group({
      proId: [x.proId, Validators.required],
      itemId: [x.itemId, Validators.required],
      itemCategoryCode: [x.itemCategoryCode, Validators.required],
      itemSubCategoryCode: [x.itemSubCategoryCode, Validators.required],
      sku: [x.skuNumber, Validators.required],
      name: [x.name, Validators.required],
      existingQty: [x.existingQty, Validators.required],
      qty: [x.addedQty, Validators.required],
      manufacturer: [x.manufacturer, Validators.required],
      model: [x.model, Validators.required],
      price: [x.price, Validators.required],
      listPrice: [x.listPrice, Validators.required],
      unit: [x.units, Validators.required],
      inventoryImage: [x.inventoryImage, Validators.required],
      color: [x.color, Validators.required],
      size: [x.size, Validators.required],
      serialNo: [x.serialNo, Validators.required],
      qrCode: [x.qrCode, Validators.required],
      vendor: [x.vendor, Validators.required],
      location: [x.location, Validators.required],
      purchaseDate: [x.purchaseDate, Validators.required],
      description: [x.description, Validators.required],
      webLinkDetails: [x.webLinkDetails, Validators.required],
    })
    this.nofileData = true

  }

