  /* cheeeck tool Name */
  mailIdVerification() {
    console.log("This is the Group Name:" + JSON.stringify(this.addTool.value.toolName))
    if (this.addTool.value.toolName == "") {
      this.toastr.warning("toolName Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      // var url = Global.url + '/checkEquipmentItemType/' + this.addEquipmentItem.value.equipmentItemType + "/" + this.loginData;
      // console.log("url" + JSON.stringify(url))
      this.http.get(Global.url + '/checktoolName/' + this.addTool.value.toolName + "/" + this.proCode)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
            // console.log("hao")
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }
