import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { Router, ActivatedRoute } from '@angular/router';

@Component({  
  providers: [Beyond],
  selector: 'app-add-service-catalog',
  templateUrl: './add-service-catalog.component.html',
  styleUrls: ['./add-service-catalog.component.scss']
})
export class AddServiceCatalogComponent implements OnInit {

  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
  }

  saveServiceCatalog(){
    this.router.navigate(['/inventory/listServiceCatalogs'])  
  }

  cancelServiceCatalog(){
    this.router.navigate(['/inventory/listServiceCatalogs'])  
  }



}
