  importDetails($event) {
    console.log("excelData" + JSON.stringify(this.excelData))
    const url = Global.url + '/proInventoryImportDetails' + '/' + this.loginData.loginCode
    this.getInventoryList()
    const formData = new FormData();
    var xhr = new XMLHttpRequest();
    formData.append('file', this.filesToUpload)
    const config = {
      headers: {
        'content-type': 'multipart/form-data'
      }
    }
    xhr.open('post', url, true);
    xhr.upload.onprogress = (e) => {
      if (e.lengthComputable) {
        this.percentage = (e.loaded / e.total) * 100;
        this.progressShow = true;
        this.progressHide = false;
        if (this.percentage == 100) {
          this.toastr.success('Data imported Successfully...')
          this.progressHide = true;
          this.displayFileModalDetail = 'none';
        }
      }
    }
    xhr.onerror = function (e) {

    };
    xhr.onload = function () {

    };
    xhr.send(formData);
  }
/* Import LOCATION DETAILS */
  CancelDetails() {
    $("#myModal2").modal("hide");
  }

  InventoryDetailspopupData(data) {
    $("#myModal2").modal("show");
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/listOfInventoryDetails/' + data.itemId + '/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.inventoryDetails = response.response;
          this.inventoryListResponse = true;
        }
        else {
          this.inventoryListResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }
