import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { ImageUpload } from '../../../utilities/imageupload'
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { Global } from '../../../global/global';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
declare var $: any;
@Component({
  providers: [Beyond],
  selector: 'app-view-po',
  templateUrl: './view-po.component.html',
  styleUrls: ['./view-po.component.scss']
})
export class ViewPoComponent implements OnInit {
  displayFileModalOrderList: string;
  loginData: any;
  displayFileView: string;
  showTruck: any = [];
  inventoryListResponse: boolean;
  addPoData: FormGroup;
  selectedInventory: any = [];
  inventoryData: any = [];
  newInventoryData: any = [];
  addnewPo: FormGroup;
  qtyData: any;
  WO: any = [];
  poId: any;
  addToPoData: boolean;
  addnewPoData: boolean;
  vendorData: any = [];
  vendorDetails: any;
  groupList: any;
  taxValue: number;
  listPriceInventory: any;
  taxPercentage: any;
  taxvalueData: any;
  totalmaterialPrice: any;
  data: any = []
  tax: any;
  priceData: any = [];
  priceValue: any;
  quantityValue: any;
  totalValue: number = 0;
  workOrderNumber: string;
  viewPoDatas: any;
  constructor(
    public beyond: Beyond,
    public router: Router,
    private route: ActivatedRoute,
    public http: Http,
    private formBuilder: FormBuilder,
    public toastr: ToastrService,
    private activatedRoute: ActivatedRoute,
  ) {
  }

  ngOnInit() {
    this.beyond.beyondAdmin();
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.poId = this.activatedRoute.snapshot.params['poId']
    this.getView()
    this.getViewInventoryData()
  }

  getView() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getPOView/' + this.poId, options)
      .subscribe(data => {
        var viewPoData = data.json();
        this.viewPoDatas = viewPoData.response[0];
        console.log("SSSSS" + JSON.stringify(this.viewPoDatas))
        this.editData(this.viewPoDatas)
        if (this.viewPoDatas.status == 200) {

        }
        else {
          this.inventoryListResponse = false;
        }
        error => {
        }
      })
  }
  editData(x) {
    this.addnewPo = this.formBuilder.group({
      customerName: [x.customerName, Validators.required],
      workOrderNumber: [x.workOrderNumber, Validators.required],
      dateCreated: [x.dateCreated, Validators.required],
      staffName: [x.staffName, Validators.required],
      custPhone: [x.custPhone, Validators.required],
      address: [x.address, Validators.required],
      state: [x.state, Validators.required],
      city: [x.city, Validators.required],
      zipCode: [x.zipCode, Validators.required],
      jobTitle: [x.jobTitle, Validators.required],
      vendor: [x.vendor, Validators.required],
      streetAddress: [x.streetAddress, Validators.required],
      vendorCity: [x.vendorCity, Validators.required],
      vendorState: [x.vendorState, Validators.required],
      vendorzipCode: [x.vendorzipCode, Validators.required],
      vendorPhone: [x.vendorPhone, Validators.required],
    });

  }
  getViewInventoryData() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getPOInventoryView/' + this.poId, options)
      .subscribe(data => {
        var selected = data.json();
        this.selectedInventory = selected.response[0];
        console.log("selectedInventory" + JSON.stringify(this.selectedInventory))
        if (this.selectedInventory.status == 200) {

        }
        else {
          this.inventoryListResponse = false;
        }
        error => {
        }
      })
  }

  editPo() {
    this.router.navigate(['/inventory/editPo', this.poId]) 
  }
}
