  mailIdVerification() {
    if (this.addPart.value.name == "") {
      this.toastr.warning("name Id is Empty")
      this.mailError = false;
      this.disableSignIn = true;
      return false;
    } else {
      this.http.get(Global.url + '/checkname/' + this.addPart.value.name + "/" + this.proCode)
        .subscribe(data => {
          var mailResponse = data.json();
          if (mailResponse.status == "TRUE") {
            this.mailError = true;
            this.disableSignIn = false;
            return false
          } else {
            this.mailError = false;
            this.disableSignIn = true;
          }
        })
    }
  }


