import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { ImageUpload } from '../../../../utilities/imageupload'
import { Global } from '../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
// import { ExportExcelService } from '../export-excel.service';
import { ExportExcelService } from '../../../vendors/export-excel.service';
import { Export2Excel } from '../../../../utilities/export2Excel';
declare var $: any;
@Component({
  providers: [Beyond, Export2Excel],
  selector: 'app-truck-order-list',
  templateUrl: './truck-order-list.component.html',
  styleUrls: ['./truck-order-list.component.scss']
})
export class TruckOrderListComponent implements OnInit {
  loginData: any;
  showTruck: any = [];
  showTruckData: boolean;
  displayFileView: string;
  name: any;
  itemId: any;
  invantoryLocation: any;
  addToOrder: FormGroup;
  displayFileModalOrderList: string;
  OlId: any;
  olData: any;
  autogenateId: any;
  autoGenID: string;
  employeeStatus: any = [];
  addToOrderData: boolean;
  listTruckData: any = [];
  generated: any;
  itemData: any;
  tolId: any;
  generateId: any;
  loginSpecific: any = [];
  staff: boolean;
  pro: boolean;
  staffId: any;
  managerData: any = [];
  managerName: any;
  managerId: any;
  managerValue: any;
  locationId: any;
  description: any;

  constructor(
    public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    public export2Excel: Export2Excel,
    private excelService: ExportExcelService
  ) {
  }
  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
    this.employeeProSpecific();
    this.getInvantoryLocation();
    this.getstaffList();
    this.getList();
    var today = this.dateConversion(new Date());
    this.addToOrder = this.formBuilder.group({
      tolId: [''],
      description: [''],
      locationId: ['', Validators.required],
      staffId: ['', Validators.required],
    })
  }

  // DATE CONVERSION
  dateConversion(todayTime) {
    var month = todayTime.getMonth() + 1;
    var day = todayTime.getDate();
    var year = todayTime.getFullYear();
    if (month < 10) month = "0" + month;
    if (day < 10) day = "0" + day;
    this.autogenateId = year + "" + month + "" + day
    return year + "-" + month + "-" + day;
  }
  //  Validation
  onSubmit3() {
    console.log("this.addToOrder.value.locationId" + this.addToOrder.value.locationId + "?" + this.addToOrder.value.staffId)
    this.addToOrderData = true;
    this.AddtoOrderList()
    // stop here if form is invalid
    if (this.addToOrder.invalid) {
      this.AddtoOrderList()
      return;
    }
  }
  get h() { return this.addToOrder.controls; }

  AddtoOrderList() {
    console.log("this.addToOrder.value.locationId" + this.addToOrder.value.locationId + "?" + this.addToOrder.value.staffId)
    if (this.addToOrder.value.locationId != '' && this.addToOrder.value.staffId != '') {
      let addObj = {
        "proId": this.loginData.loginCode,
        "locationId": this.addToOrder.value.locationId,
        "staffId": this.addToOrder.value.staffId,
        "tolId": this.autoGenID,
        "managerName": this.managerName == undefined ? 0 : this.managerName,
        "managerId": this.managerId == undefined ? 0 : this.managerId,
        "description": this.addToOrder.value.description,
        "createdOn": new Date(),
        "createdBy": '' + this.loginData.emailId,
      }
      console.log("OBJ" + JSON.stringify(addObj))
      this.http.post(Global.url + '/addToOrderData', addObj)
        .subscribe(data => {
          var inventoryLog = data.json();
          if (inventoryLog.status == 200) {
            this.displayFileModalOrderList = 'none'
            this.addToOrderData = false;
            this.addToOrder.reset()
            this.getList()
            this.toastr.success('Added New Truck  Order  List Successfuly...');
          } else {
          }
        }, error => {
          console.log(error);// Error getting the data
        });

    }
  }


  staffManger() {
    this.staffId = this.addToOrder.value.staffId.slice(0, 11)
    console.log("ASSS" + this.staffId)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/vanstaffData/' + this.loginData.loginCode + "/" + this.addToOrder.value.staffId, options)
      .subscribe(data => {
        var proStaff = data.json();
        if (proStaff.status == 200) {
          console.log("group Details" + JSON.stringify(proStaff))
          this.managerData = proStaff.response;
          this.managerName = this.managerData[0].managerName
          this.managerId = this.managerData[0].managerId
          console.log("managerId" + JSON.stringify(this.managerName))
          console.log("managerName" + JSON.stringify(this.managerData[0].managerName))
          let result = this.employeeStatus.filter(el => el.employeeId === this.addToOrder.value.staffId

          )
          console.log("RESULTt" + JSON.stringify(result))

          // return false;
          // this.currentStatus1 = true;
        }
        else {
          // this.currentStatus1 = false;
          this.toastr.warning('Unable to connect to server');
        }
      })
  }
  getautogneratedId() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + "/generateOlID/" + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.generated = response.response;
          this.OlId = this.generated[0].tolId
          if (this.OlId == 0) {
            this.autoGenID = this.autogenateId + "-" + 1
          }
          if (this.OlId != 0) {
            this.generateId = parseInt('1') + parseInt(this.OlId)
            this.autoGenID = this.autogenateId + "-" + this.generateId
          }
        }
        else {

        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }
  /* -- Employee staff pro Specific data-- */
  employeeProSpecific() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/VanStaffStatus/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var proStaff = data.json();
        if (proStaff.status == 200) {
          this.employeeStatus = proStaff.response;
          // return false;
          // this.currentStatus1 = true;
        }
        else {
        }
      })
  }

  getList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getListOrder/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var proStaff = data.json();
        if (proStaff.status == 200) {
          this.listTruckData = proStaff.response;
          this.showTruckData = true;
        }
        else {
          this.showTruckData = false;
        }
      })
  }
  getstaffList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getstaffListOrder/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var proStaff = data.json();
        if (proStaff.status == 200) {
          this.listTruckData = proStaff.response;
          console.log("SJ" + JSON.stringify(this.listTruckData))
          this.showTruckData = true;
        }
        else {
          this.showTruckData = false;
        }
      })
  }

  getInventoryList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewTruckData/' + this.itemData, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.showTruck = response.response;
          console.log("SJ" + JSON.stringify(this.showTruck))
          this.itemId = this.showTruck[0].staffId
          this.tolId = this.showTruck[0].tolId
          this.name = this.showTruck[0].location
          this.showTruckData = true;
        }
        else {
          this.showTruckData = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }


  viewOrders(data) {
    this.itemData = data.itemId
    if (data.itemId == null) {
      alert("There are no items in order list")
    }
    else {
      this.displayFileView = 'block'
      this.getInventoryList()
      this.itemData = data.itemId
      this.managerValue = data.managerId
      this.managerName = data.managerName
      this.tolId = data.tolId
      this.locationId = data.locationId
      this.description = data.description
      this.staffId = data.staffId
    }
  }
  save() {
   let addObj = {
      "proId": this.loginData.loginCode,
      "locationId": this.locationId,
      "staffId": this.staffId,
      "tolId": this.tolId,
      "managerName": this.managerName == undefined ? '' : this.managerName,
      "managerId": this.managerValue == undefined ? '' : this.managerValue,
      "description": this.description,
      "modifiedOn": new Date(),
      "modifiedBy": '' + this.loginData.emailId,
    }
    console.log("OBJ" + JSON.stringify(addObj))
    this.http.post(Global.url + '/updateTruckReject', addObj)
      .subscribe(data => {
        var inventoryLog = data.json();
        if (inventoryLog.status == 200) {
          this.displayFileView = 'none'
          this.addToOrderData = false;
          this.getList()
          this.toastr.success('Save Truck Order  List Successfuly...');
        } else {
        }
      }, error => {
        console.log(error);// Error getting the data
      });

  }
  updateSubmit() {
    let addObj = {
      "proId": this.loginData.loginCode,
      "locationId": this.locationId,
      "staffId": this.staffId,
      "tolId": this.tolId,
      "managerName": this.managerName == undefined ? '' : this.managerName,
      "managerId": this.managerValue == undefined ? '' : this.managerValue,
      "description": this.description,
      "modifiedOn": new Date(),
      "modifiedBy": '' + this.loginData.emailId,
    }
    console.log("OBJ" + JSON.stringify(addObj))
    this.http.post(Global.url + '/updateTruckSubmit', addObj)
      .subscribe(data => {
        var inventoryLog = data.json();
        if (inventoryLog.status == 200) {
          this.displayFileView = 'none'
          this.addToOrderData = false;
          this.getList()
          this.toastr.success('Submitted  Truck Order  List Successfuly...');
        } else {
        }
      }, error => {
        console.log(error);// Error getting the data
      });
  }
  approve() {
    let addObj = {
      "locationId": this.locationId,
      "staffId": this.staffId,
      "tolId": this.tolId,
      "managerName": this.managerName == undefined ? '' : this.managerName,
      "managerId": this.managerValue == undefined ? '' : this.managerValue,
      "description": this.description,
      "modifiedOn": new Date(),
      "modifiedBy": '' + this.loginData.emailId,
    }
    console.log("OBJ" + JSON.stringify(addObj))
    this.http.post(Global.url + '/updateTruck', addObj)
      .subscribe(data => {
        var inventoryLog = data.json();
        if (inventoryLog.status == 200) {
          this.displayFileView = 'none'
          this.addToOrderData = false;
          this.getList()
          this.toastr.success('Approved Truck Order   List Successfuly...');
        } else {
        }
      }, error => {
        console.log(error);// Error getting the data
      });

    //  }
  }
  updateTruckReject() {
    let addObj = {
      "proId": this.loginData.loginCode,
      "locationId": this.locationId,
      "staffId": this.staffId,
      "tolId": this.tolId,
      "managerName": this.managerName == undefined ? '' : this.managerName,
      "managerId": this.managerValue == undefined ? '' : this.managerValue,
      "description": this.description,
      "modifiedOn": new Date(),
      "modifiedBy": '' + this.loginData.emailId,
    }
    console.log("OBJ" + JSON.stringify(addObj))
    this.http.post(Global.url + '/updateTruckReject', addObj)
      .subscribe(data => {
        var inventoryLog = data.json();
        if (inventoryLog.status == 200) {
          this.displayFileView = 'none'
          this.addToOrderData = false;
          this.getList()
          this.toastr.success('Rejected  Truck Order  List Successfuly...');
        } else {
        }
      }, error => {
        console.log(error);// Error getting the data
      });

    //  }
  }
  addOrders() {
    this.displayFileModalOrderList = 'block'
    this.getautogneratedId()
  }
  cancelOrderList() {
    this.displayFileModalOrderList = 'none'
  }

  CancelDetails() {
    this.displayFileView = 'none'
  }

  home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  list() {
    this.router.navigate(['/pricebook/listPriceBook'])
  }
  getInvantoryLocation() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/inventoryLoccation/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.invantoryLocation = response.response;
        }
        else {
          // this.priceBookServiceResponse = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }
}
