import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../../utilities/beyond';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  providers: [Beyond],
  selector: 'app-edit-service-catalog',
  templateUrl: './edit-service-catalog.component.html',
  styleUrls: ['./edit-service-catalog.component.scss']
})
export class EditServiceCatalogComponent implements OnInit {

  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
    ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
  }

  saveServiceCatalog(){
    this.router.navigate(['/inventory/listServiceCatalogs'])  
  }

  cancelServiceCatalog(){
    this.router.navigate(['/inventory/listServiceCatalogs'])  
  }


}
