import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond'
import { ActivatedRoute, Router } from '@angular/router';
import { ImageUpload } from '../../../../utilities/imageupload'
import { Global } from '../../../../global/global';
import { Http, Headers, RequestOptions } from '@angular/http';
import { ToastrService } from 'ngx-toastr';
import { FormGroup, FormBuilder, Validators, FormControl, FormArray } from '@angular/forms';
// import { ExportExcelService } from '../export-excel.service';
import { ExportExcelService } from '../../../vendors/export-excel.service';
import { Export2Excel } from '../../../../utilities/export2Excel';
declare var $: any;
@Component({
  providers: [Beyond, Export2Excel],
  selector: 'app-list-po',
  templateUrl: './list-po.component.html',
  styleUrls: ['./list-po.component.scss']
})
export class ListPoComponent implements OnInit {
  displayFileModalOrderList: string;
  loginData: any;
  displayFileView: string;
  listPo: any =[];

  constructor(
    public beyond: Beyond,
    public router: Router,
    private formBuilder: FormBuilder,
    private activatedRoute: ActivatedRoute,
    public http: Http,
    public toastr: ToastrService,
    public export2Excel: Export2Excel,
    private excelService: ExportExcelService
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
    this.loginData = JSON.parse(localStorage.getItem("login"))
this.getPOList()
  }


  viewOrders(poId) {
    this.router.navigate(['/inventory/viewPo',poId])
  }
  editPo(poId) {
    this.router.navigate(['/inventory/editPo', poId]) 
  }
  CancelDetails() {
    
  }
  home() {
    this.router.navigate(['/proz-dashboard/prozDashboard/', this.loginData.loginCode]);
  }
  getPOList() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getPOList/' + this.loginData.loginCode, options)
      .subscribe(data => {
        var response = data.json();
        this.listPo = response.response;
       }, error => {
        this.toastr.warning('Unable to connect to server');
      })
  }
}
