import { Component, OnInit } from '@angular/core';
import { Beyond } from '../../../../utilities/beyond';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  providers: [Beyond],
  selector: 'app-adjust-qty-inventory',
  templateUrl: './adjust-qty-inventory.component.html',
  styleUrls: ['./adjust-qty-inventory.component.scss']
})
export class AdjustQtyInventoryComponent implements OnInit {

  constructor(
    public beyond: Beyond,
    public router: Router,
    private activatedRoute: ActivatedRoute,
  ) { }

  ngOnInit() {
    this.beyond.beyondAdmin()
  }

  backToViewInventory() {
    this.router.navigate(['/inventory/viewInventory'])
  }

}
