  fetchInventory() {
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/viewInventoryData' + '/' + this.itemId, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.inventoryData = response.response[0];
          this.urls = this.inventoryData.inventoryImage.split(',')
          this.showInventoryStatus = true;
          this.InventoryDetailspopupData()
          //   this.priceBookServiceResponse = true;
          //   this.editData(this.viewInventoryData)
          //   this.getInvantorySubCategory(this.viewInventoryData.itemCategoryCode)

          this.urls = this.inventoryData.inventoryImage.split(',')
          console.log("urls" + JSON.stringify(this.inventoryData.inventoryImage))
          if (this.inventoryData.inventoryImage != "") {
            this.withImage = true;
            this.withOutImage = false;
            for (var i = 0; i < this.urls.length; i++) {
              this.imageArray.push(this.urls[i])
              this.imageNames.push({ image: this.urls[i] });
              this.arrayValues.push(this.urls[i])
              this.imagePreview = Global.url + "/" + this.urls[i]
              this.arr.push({ name: this.imagePreview });
            }
            this.uniqueArray = Array.from(new Set(this.arr));
            console.log(" this.uniqueArray" + JSON.stringify(this.uniqueArray))

          }
          console.log("arrarr" + JSON.stringify(this.arr))
          if (this.inventoryData.inventoryImage == "" || this.inventoryData.inventoryImage == null || this.inventoryData.inventoryImage == undefined || this.inventoryData.inventoryImage == "undefined") {
            this.withOutImage = true;
            this.withImage = false;
            this.imagePreview = '../../assets/img/no-image.png'
          }
          if (this.inventoryData.qrCode == '' || this.inventoryData.qrCode == null || this.inventoryData.qrCode == undefined) {
            this.qrCodeData = "assets/img/QrCode.png"

            console.log("qrCodeData" + this.qrCodeData)
          } else {
            this.qrCodeData = Global.url + "/" + this.inventoryData.qrCode
            // resume1http://localhost:8888/uploads/33f46c179cb54c20bf5a0a5a05ea8188
            console.log("resume1" + this.qrCodeData)
          }

        }
        else {
          this.showInventoryStatus = false;
        }
        error => {
          //console.log('The execution is failed ' + JSON.stringify(error))
        }
      })
  }
  dateConversion(todayTime) {
    var month = todayTime.getMonth() + 1;
    var day = todayTime.getDate();
    var year = todayTime.getFullYear();
    if (month < 10) month = "0" + month;
    if (day < 10) day = "0" + day;
    return day + "-" + month + "-" + year;
  }

