  LocationWiseTypeData(location) {
    this.locationname = location
    this.checkingLocation()
    if (location == this.locationValue) {
      this.locationError = true;
    }
    else {
      this.locationError = false; this.locationError = false;
      this.location = location
      var headers = new Headers();
      headers.append("Accept", 'application/json');
      headers.append('Content-Type', 'application/json');
      let options = new RequestOptions({ headers: headers });
      this.http.get(Global.url + '/InventoryLocationWiseData/' + this.inventoryData.itemId + '/' + this.inventoryData.name + '/' + location, options)
        .subscribe(data => {
          var response = data.json();
          if (response.status == 200) {
            this.locationwiseData = response.response;
            this.itemId = this.locationwiseData[0].itemId;
            // this.totalQuantityNew = this.locationwiseData[0].totalQuantity;
            // console.log("TESTING"+this.totalQuantityNew)
            this.existingValue = this.locationwiseData[0].existingQty;
            this.addedQtyData = this.locationwiseData[0].addedQty;
          }
          else {
            this.existingQty = 0
            this.addedQtyData = 0;
          }
          error => {
          }
        })
    }
  }
  checkingLocation() {
    console.log("LLLLLLLLLLL" + this.locationname)
    var headers = new Headers();
    headers.append("Accept", 'application/json');
    headers.append('Content-Type', 'application/json');
    let options = new RequestOptions({ headers: headers });
    this.http.get(Global.url + '/getmovelocations/' + this.loginData.loginCode + '/' + this.locationname, options)
      .subscribe(data => {
        var response = data.json();
        if (response.status == 200) {
          this.newLocation = response.response;
          if (this.newLocation.length > 0) {
            this.Qty = this.newLocation[0].totalQuantity;
            console.log("Not Equal ZERO CALLING" + this.Qty)
          }
        }
        if (this.newLocation.length == 0) {
          this.Qty = 0;
          console.log("ZERO CALLING" + this.Qty)
        }

        else {
          this.existingQty = 0
          this.addedQtyData = 0;
        }
        error => {
        }
      })

  }
