  quantityToadd(qtyData) {
    this.quantityAdd = qtyData
    if (parseInt(this.totalQuantityNew) == 0 && this.moveLocation.value.moveQuantity != "") {
      this.zeroError = true
    }
    else if (this.moveLocation.value.moveQuantity == "") {
      this.zeroError = false
      this.showError = false
    }

    else if (parseInt(this.quantityAdd) > parseInt(this.totalQuantityNew)) {
      this.showError = true
    }
    else {
      this.showError = false
      this.zeroError = false
    }
  }

